.weather-box-list{
    display: flex;
    width: 95%;
    margin: auto;
    padding: 30px 0;
}
.weather-box{
    width: 20%;
    margin: auto;
    background: #ffffffbd;
    border-radius: 10px;
    box-shadow: 2px 2px 5px gray;
    padding: 10px;
    display: flex;
    justify-content: center;
}